body {
  background-color: #f0f0f0;
  margin: 20px;
  font-family: Arial, sans-serif;
}

.loader, .error {
  text-align: center;
  background-color: #fff;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.cat-info {
  text-align: center;
  background-color: #fff;
  border-radius: 5px;
  margin-top: 20px;
  padding: 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.breed-select {
  width: 200px;
  height: 35px;
  border: 1px solid #ccc;
